.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  max-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header a {
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
}


.App-link {
  color: #ffff;
  size: 10px;
}


